import { youtubeProvider } from './../../providers/youtube/youtube';
import { Component } from '@angular/core';
import { NavController, AlertController} from 'ionic-angular';
import { Observable } from 'rxjs/Observable';
import { PlaylistPage } from '../playlist/playlist';
 
/**
 * Generated class for the VideoPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-video',
  templateUrl: 'video.html',
})
export class VideoPage {

  channelId = 'UCZZPgUIorPao48a1tBYSDgg'; 
  playlists: Observable<any[]>;
 
  constructor(public navCtrl: NavController, private youtubeProvider: youtubeProvider, private alertCtrl: AlertController) { }
 
  searchPlaylists() {
    this.playlists = this.youtubeProvider.getPlaylistsForChannel(this.channelId);
    this.playlists.subscribe(data => {
      console.log('playlists: ', data);
    }, err => {
      let alert = this.alertCtrl.create({
        title: 'Error',
        message: 'No Playlists found for that Channel ID',
        buttons: ['OK']
      });
      alert.present();
    })
  }
 
  openPlaylist(id) {
    this.navCtrl.push(PlaylistPage, {id: id});
  }
}



